.icon {
	text-decoration: none;

	&:before {
		display: inline-block;
		font-family: FontAwesome;
		font-size: 1em;
		text-decoration: none;
		font-style: normal;
		font-weight: normal;
		margin-right: 8px;
		position: relative;
		-webkit-font-smoothing:antialiased;
		-moz-osx-font-smoothing:grayscale;
	}

	&.icon--fw:before {
		width: 1.28571429em;
		text-align: center;
	}
}

.hidden {
  @include hidden;
}

.visuallyhidden {
  @include visuallyhidden;
}